<template>
  <div class="wedding-invitation" :class="{ 'invitation-bounce': canOpen }">
    <div class="invitation-container" :class="{ 'invitation-down': isOpening }">
      <div class="invitation-cover">
        <div class="cover-content" :class="{ 'invitation-up': isOpening }">
          <div class="content-inside">
            <img class="content-inside-photo" src="../images/photo.jpg" />
            <p>我们要结婚啦！</p>
            <p>张静 & 冯子吟</p>
            <p>时间：10/17/2020</p>
            <p>地点：合肥绿地福朋喜来登酒店</p>
            <div class="content-inside-bless">
              <input
                type="email"
                placeholder="快输入你的邮箱～"
                @focus="(isEditing = true), (isValidEmail = true)"
                v-model="email"
                ref="emailInput"
              />
              <p v-if="!isEditing && !isValidEmail">
                邮箱好像不太对！
              </p>
              <input
                placeholder="告诉我你是谁！"
                v-model="name"
                @focus="isEditing = true"
                ref="nameImput"
              />
              <p v-if="!isEditing && !name">
                留下名来！
              </p>
              <input
                type="number"
                placeholder="务必带上你的伴侣！"
                @focus="isEditing = true"
                v-model="numberOfPeople"
                ref="numberInputInput"
                min="1"
              />
              <div>
                <button @click="sendEmails">我要参加！</button>
              </div>
            </div>
          </div>
        </div>
        <div class="cover-inside-left" :class="{ opening: isOpening }"></div>
        <div class="cover-inside-right" :class="{ opening: isOpening }"></div>
        <img
          class="cover-inside-seal"
          src="../images/seal.png"
          @click="openInvitation"
          :class="{ 'invitation-flight': isOpening }"
        />
      </div>
    </div>
  </div>
</template>

<script>
import request from "superagent";
export default {
  props: ["canOpen"],
  data() {
    return {
      isOpening: false,
      email: "",
      name: "",
      numberOfPeople: "",
      isEditing: true,
      isValidEmail: true
    };
  },
  methods: {
    // 打开邀请函
    openInvitation() {
      this.isOpening = true;
    },
    closeInvitation() {
      this.isOpening = false;
      setTimeout(() => {
        this.$emit("onClose");
      }, 660);
    },
    sendEmails() {
      this.$nextTick(() => {
        this.isEditing = false;
        if (!this.email || !this.validateEmail(this.email)) {
          this.isValidEmail = false;
        } else {
          request
            .post(
              "https://6enpngw55h.execute-api.us-east-1.amazonaws.com/wedding"
            )
            .send({
              name: this.name,
              email: this.email,
              count: this.numberOfPeople
            })
            .then(res => {
              alert("yay got " + JSON.stringify(res.body));
            });
        }
      });
    },
    validateEmail(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    }
  }
};
</script>

